._pseudo {
  color: #698f3f;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Inter, Arial, Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

h2, h3, h4 {
  font-weight: 500;
}

ul, nav, menu {
  list-style: none;
}

input::-webkit-outer-spin-button {
  appearance: none;
}

input::-webkit-inner-spin-button {
  appearance: none;
}

a, a:hover, a:link, a:visited {
  color: #000;
  text-decoration: none;
}

:focus {
  outline: none;
}

.container {
  grid-template-rows: auto 1fr auto;
  height: 100vh;
  display: grid;
}

.content-stripe {
  max-width: 1024px;
  margin: auto;
}

button, a.button {
  color: #fff;
  cursor: pointer;
  background-color: #20991e;
  border: none;
}

input {
  border: none;
  margin-bottom: 10px;
  padding: 3px 5px;
  font-size: 1.1em;
  display: block;
}

textarea {
  resize: none;
  padding: 5px;
  font-family: inherit;
}

.hidden {
  display: none;
}

.alert-window {
  z-index: 10;
  color: #fff;
  text-align: center;
  opacity: 0;
  background-color: #5ea135;
  width: 100vw;
  padding: 20px;
  transition: opacity .5s;
  position: fixed;
  top: 0;
}

.alert-window.error {
  background-color: red;
}

.header-top {
  border-bottom: 1px solid #000;
  padding: 30px 0;
}

.header-top .content-stripe {
  justify-content: space-between;
  align-items: center;
  min-height: 60px;
  padding-right: 20px;
  display: flex;
}

.logo {
  height: 40px;
}

.logo a {
  height: inherit;
  display: inline-block;
}

.logo img {
  height: inherit;
}

.user-nav nav {
  align-items: center;
  gap: 10px;
  display: flex;
}

.user-nav li {
  line-height: 0;
}

.user-nav img {
  border: 2px solid #fff;
  border-radius: 50%;
  outline: 3px solid gray;
  width: 60px;
  height: 60px;
  display: inline-block;
}

.user-nav a {
  line-height: 0;
  display: inline-block;
}

.user-nav a:hover {
  color: #5ea135;
}

.user-nav .profile {
  border-radius: 50%;
}

.user-nav .access-btn {
  cursor: pointer;
  border-block: 2px solid #000;
  padding: 15px 20px;
}

.hero {
  background-image: url("hero_bg.0a101ea1.jpg");
  background-position: center;
  background-size: cover;
  height: 460px;
}

.hero .content-stripe {
  height: inherit;
  grid-template-rows: 1fr 50px;
  display: grid;
}

.hero-message {
  padding: 50px 0 0 15%;
  font-size: 2.5em;
  line-height: 130%;
}

.tail-text {
  color: #aa7d1c;
  opacity: 1;
  transition: all 1.2s;
  display: inline-block;
  position: relative;
  transform: translateX(0);
}

.reverse {
  opacity: 0;
  transform: translateX(50px);
}

.hero-searchbar {
  align-self: end;
  gap: 5px;
  display: flex;
}

.hero-searchbar input, .hero-searchbar .btn-search {
  border: 1px solid #eaeaea;
  height: 35px;
}

.hero-searchbar input {
  width: 35%;
  padding-inline: 15px;
  display: inline;
}

.hero-searchbar .btn-search {
  padding-inline: 15px;
}

main {
  margin-block: 5px;
}

.welcome {
  text-align: center;
  padding-block: 90px;
  line-height: 150%;
}

.welcome article {
  width: 80%;
  max-width: 800px;
  display: inline-block;
}

.welcome h1 {
  font-size: 1.5em;
}

.welcome p {
  margin-top: 30px;
}

.cta {
  margin-bottom: 15px;
  position: relative;
}

.cta-bg {
  width: calc(50% + 50px);
  height: 100%;
  position: absolute;
}

.cta-bg-contribute {
  z-index: -5;
  background-color: #f4f3f2;
  background-image: url("cta_contribute_bg.691a294a.jpg");
  background-position: center;
  background-size: cover;
  border: 2px solid #ffc851;
  border-radius: 0 30px 30px 0;
}

.cta-bg-leader {
  z-index: -10;
  background-image: url("cta_leader_create_bg.c5907914.jpg");
  background-position: center;
  background-size: cover;
  border-radius: 30px 0 0 30px;
  margin-top: 20px;
  right: 0;
}

.cta .content-stripe {
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
}

.cta-box {
  background-color: #fff9;
  width: 400px;
  margin: 70px 0 40px;
}

.cta-description {
  text-shadow: 0 0 1px #00000080;
  padding: 35px;
  padding-bottom: 50px;
}

.cta-description h2 {
  margin-bottom: 50px;
}

.contribute .cta-description h2 {
  text-align: right;
}

.cta-description p {
  text-align: justify;
  margin-inline: 20px;
}

.cta-action {
  padding-top: 50px;
  text-align: right;
  padding-block: 20px;
  padding-right: 20px;
}

.cta-action a {
  padding: 8px 27px;
}

.impact {
  min-height: 200px;
  margin-top: 120px;
  position: relative;
}

.impact-bg {
  z-index: -5;
  opacity: .35;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 125px));
  background-color: #804e49;
  background-image: url("impact_bg.f6804283.jpg");
  background-position: center;
  background-size: cover;
  width: 95%;
  height: calc(100% + 125px);
  position: absolute;
}

.impact-bg:after {
  content: "";
  background: linear-gradient(0deg, #fff0 30%, #fff 60%);
  width: 100%;
  height: 100%;
  display: block;
}

.impact-description, .stat-content {
  padding: 50px;
}

.impact-description h2 {
  text-align: right;
  margin-bottom: 70px;
}

.impact-description p {
  text-align: justify;
  width: 75%;
  line-height: 1.8;
}

.impact-interaction {
  text-align: right;
  margin-top: 30px;
}

.impact-interaction a {
  padding: 10px 15px;
}

.stat {
  min-height: 200px;
  margin-bottom: 30px;
  position: relative;
}

.stat-bg {
  z-index: -5;
  clip-path: polygon(0 0, 100% 125px, 100% 100%, 0 100%);
  background-color: #fff;
  width: 95%;
  height: calc(100% + 50px);
  position: absolute;
  top: -50px;
  right: 0;
}

.stat-bg:before {
  content: "";
  width: 100%;
  height: 100%;
  position: inherit;
  opacity: .3;
  background-image: url("stat_bg.632d30c8.jpg");
  background-position: center;
  background-size: cover;
  display: block;
}

.stat-bg:after {
  content: "";
  position: inherit;
  background: linear-gradient(#fff0 50%, #fff 90%);
  width: 100%;
  height: 100%;
  display: block;
}

.stat h2 {
  text-align: left;
  margin-bottom: 50px;
}

.stat p {
  text-align: justify;
  width: 80%;
  line-height: 1.5;
}

.stat-table-holder {
  margin-top: 50px;
  justify-content: center;
  align-items: flex-end;
  width: 95%;
  margin-inline: auto;
  display: flex;
}

.stat-table {
  text-align: left;
  z-index: 0;
  margin-left: -20px;
  padding: 20px;
  position: relative;
  overflow: hidden;
  box-shadow: -3px -2px 5px #808080a6;
}

.stat-table:first-child {
  width: 400px;
  height: 300px;
  margin-left: 0;
}

.stat-table:nth-child(2) {
  width: 350px;
  height: 280px;
}

.stat-table:nth-child(3) {
  width: 300px;
  height: 260px;
}

.stat-table-bg {
  z-index: -5;
  background-color: #fff;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.stat-table-bg-img {
  opacity: 1;
  background-position: 0;
  background-size: cover;
  width: 100%;
  height: 100%;
}

.stat-table-bg-shadow {
  background-color: #fff;
  border-radius: 15px;
  width: calc(100px + 65%);
  height: calc(100% - 30px);
  position: absolute;
  top: 45px;
  right: -100px;
  transform: skewX(349deg);
  box-shadow: inset 0 0 13px gray;
}

.stat-table:first-child .stat-table-bg-img {
  background-image: url("stat_01.5c02ac51.jpg");
}

.stat-table:nth-child(2) .stat-table-bg-img {
  background-image: url("stat_02.6c39af53.jpg");
}

.stat-table:nth-child(3) .stat-table-bg-img {
  background-image: url("stat_03.0aaa7688.jpg");
}

.stat-table h3 {
  text-indent: 15px;
  color: #fff;
  text-shadow: 0 0 5px #000;
  text-transform: capitalize;
  margin-bottom: 30px;
}

.stat-list {
  width: 80%;
}

.stat-list li {
  justify-content: space-between;
  margin-bottom: 10px;
  display: flex;
}

.stat-list li span:first-child {
  color: #fff;
  text-shadow: 0 0 5px #000;
  text-transform: capitalize;
}

.review {
  background-color: #f3f3ea;
  border-block: 2px solid #e7e7bd;
  padding: 50px;
}

.review-slider {
  justify-content: space-evenly;
  display: flex;
}

.review-box {
  text-align: center;
  border-inline: 1px solid #c19000;
  width: 100%;
  max-width: 300px;
  margin-inline: 30px;
  padding: 15px 20px;
}

.review-box-head {
  margin-bottom: 15px;
  justify-content: center;
  width: 70%;
  margin-inline: auto;
  display: flex;
  position: relative;
}

.review-box-head svg {
  filter: blur(2px);
  transform: rotate(-90deg);
}

.review-box-head img {
  border-radius: 50%;
  align-self: center;
  width: 83%;
  position: absolute;
}

.review-box-name {
  margin-bottom: 15px;
  font-weight: 700;
}

.review-box-review {
  font-size: .9rem;
}

footer {
  background-color: gray;
}

footer .content-stripe {
  color: #fff;
  flex-wrap: wrap;
  justify-content: center;
  padding: 30px 30px 10px;
  font-size: .7rem;
  display: flex;
}

footer .content-stripe > :nth-child(n) {
  width: 50%;
}

footer .content-stripe h2 {
  font-size: 1.2em;
}

footer .content-stripe a {
  text-decoration: none;
}

footer .content-stripe ul, footer .content-stripe nav {
  padding: 0;
}

footer .content-stripe nav {
  text-align: right;
}

footer .content-stripe ul {
  width: fit-content;
}

footer .content-stripe ul li {
  white-space: nowrap;
  flex-wrap: nowrap;
  justify-content: space-between;
  column-gap: 10px;
  display: flex;
}

.reach-contact, .reach-contact a {
  color: #ffffffbf;
}

.reach-contact a:hover {
  text-decoration: underline;
}

footer .content-stripe nav a {
  color: #d0d0d0;
  text-transform: uppercase;
  text-shadow: 0 0 4px #ffffff4a;
  border-right: 2px solid #ffa60000;
  padding-right: .5em;
  line-height: 3em;
}

footer .content-stripe nav a:hover {
  color: orange;
  border-color: orange;
  font-weight: bold;
}

.copyright {
  text-align: center;
  color: #ffffff45;
  -webkit-user-select: none;
  user-select: none;
}
